#nav-container{
    position: fixed;
    width: 100%;
    background: var(--dark-blue);
    display: flex;
    justify-content: center;
    height: 50px;
    z-index: 10;
    box-shadow: 0px 1px 3px #252525d6;
}

#nav-center{
    display: flex;
    align-items: center;
}

.hamburguer{
    display: none;
    width: 40px;
    justify-content: center;
    align-items: center;
    margin-left: 12px;
    cursor: pointer;
}

.hamburguer img{
    width: 35px;
    height: 29px;
}

.close{
    display: none;
    width: 90%;
    justify-content: flex-end;
    align-items: center;
    cursor: pointer;
    margin-bottom: 15px;
}

.close img{
    width: 35px;
    height: 29px;
    margin-right: 15px;
}

.nav-link{
    padding: 8px 7px;
    margin: 0 10px;
    font-size: 18px;
    position: relative;
    cursor: pointer;
}

.nav-link:before{
    position: absolute;
    content: '';
    height: 0;
    width: 0;
    border: 3px solid transparent;
    bottom: 0;
    left: 0;
    box-sizing: border-box;
}

.nav-link:after{
    position: absolute;
    content: '';
    height: 0;
    width: 0;
    border: 3px solid transparent;
    top: 0;
    right: 0;
    box-sizing: border-box;
}

.nav-link:hover:before{
    height: 100%;
    width: 100%;
    border-right: none;
    border-bottom: none;
    transition: height .2s ease-out, width .2s ease-out .2s;
    border-color: var(--amber);
}

.nav-link:hover:after{
    height: 100%;
    width: 100%;
    border-left: none;
    border-top: none;
    transition: height .2s ease-out, width .2s ease-out .2s;
    border-color: var(--amber);
}

.nav-link a{
    text-decoration: none;
    color: var(--marmol);
    cursor: pointer;
}

@media screen and (max-width: 780px){
    #nav-container{
        justify-content: flex-start;
    }

    #nav-center{
        position: fixed;
        display: flex;
        flex-direction: column;
        align-items: center;
        background: var(--dark-blue);
        width: 100%;
        height: 100vh;
        padding: 30px 10px;
        transition: .3s ease-in;
        left: -200%;
    }

    #nav-center.active{
        left: 0;
    }

    .hamburguer{
        display: flex;
    }

    .close{
        display: flex;
    }

    .nav-link{
        padding: 10px 0px;
        cursor: pointer;
        width: 80%;
        display: flex;
        justify-content: center;
    }
}
#hero-container{
    margin-top: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    /* background: var(--light); */
    padding: 40px 0 65px 0;
    flex-direction: column;
    position: relative;
}

#hero-container canvas{
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 0;
    background: var(--light);
}

.hero-text{
    /* background: white; */
    padding: 30px;
    text-align: center;
    z-index: 1;
}

.hero-title{
    font-size: 60px;
    font-family: var(--sans-pro);
    font-weight: bolder;
    z-index: 1;
    line-height: 0;
}

.hero-description{
    font-size: 23px;
    font-style: italic;
    margin-bottom: 0.9rem;
    z-index: 1;
}

.social-container{
    display: flex;
    justify-content: space-around;
    align-items: center;
    z-index: 1;
}

.social-item{
    width: 40px;
    height: 40px;
    padding: 9px;
    border-radius: 50%;
    transition: .3s;
    border: 1px solid transparent;
}

.social-item:hover{
    border-color: var(--black);
}

.social-item img{
    width: 100%;
}

.hero-button-container{
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 15px;
    z-index: 1;
}

.learn-more-button{
    padding: 10px 13px;
    border-radius: 40px;
    cursor: pointer;
    background: var(--amber);
    border: 2px solid var(--black);
    transition: .3s;
    font-size: 18px;
    font-weight: bold;
    outline: none;
}

.learn-more-button:hover{
    background: var(--light);
    border-color: var(--amber);
}

