    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 40px;
    gap: 40px;
    text-align: center;
    margin-top: 10px;
}

.skills-type{
    padding: 15px;
    background: var(--black);
    color: white;
    border-radius: 0.8rem;
    margin-top: 15px;
    transition: .3s;
    box-shadow: 0 0 1px 1px var(--dark-blue);
    border: 3px solid var(--dark-blue);
}

.skills-type:hover{
    transform: scale(1.02);
    box-shadow: 0px 5px 2px 2px var(--black);
}

/* ===== skill item ===== */
.skill{
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

