}

.skill-percentage-container{
    display: flex;
    align-items: center;
}


@media screen and (max-width: 770px){
    .skills-container{
        grid-template-columns: 1fr;
        grid-gap: 20px;
        gap: 20px;
    }
}
#portfolio{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 50px 0;
    background: var(--dark-blue);
}

.projects-container{
    display: grid;
    grid-gap: 20px 40px;
    gap: 20px 40px;
    grid-template-columns: repeat(3, 400px);
    grid-template-rows: repeat(2, 209px);
    padding: 45px 0;
}

.project{
    border-radius: 7px;
    transition: .3s;
    border: 2px solid var(--amber);
    box-shadow: 0px 0px 1px 0px var(--amber);
    will-change: transform;
    position: relative;
    overflow: hidden;
    background: var(--white);
}

.project:hover{
    transform: translateY(-10px);
    box-shadow: 0px 7px 2px 1px #a97f01;
}

.project:hover .project-overlay{
    opacity: 1;
}

.project-image{
    width: 400px;
    height: 209px;
}

.project-overlay{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    background: #000000bb;
    opacity: 0;
    transition: .2s;
}

.project-overlay a{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    text-decoration: none;
}

.project-text{
    color: #fff;
    text-align: center;
    padding: 0 15px;
    font-size: 17px;
}

.project-title{
    margin: 16px 0 10px 0px;
}

