.lds-ring {
    display: flex;
    position: relative;
    width: 100%;
    justify-content: center;
    align-items: center;
    height: 30px;
  }
  .lds-ring div {
    box-sizing: border-box;
    display: block;
    position: absolute;
    width: 40px;
    height: 40px;
    margin: 0;
    border: 5px solid #2a2a2a;
    border-radius: 50%;
    -webkit-animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
            animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    border-color: #2a2a2a transparent transparent transparent;
  }
  .lds-ring div:nth-child(1) {
    -webkit-animation-delay: -0.45s;
            animation-delay: -0.45s;
  }
  .lds-ring div:nth-child(2) {
    -webkit-animation-delay: -0.3s;
            animation-delay: -0.3s;
  }
  .lds-ring div:nth-child(3) {
    -webkit-animation-delay: -0.15s;
            animation-delay: -0.15s;
  }
  @-webkit-keyframes lds-ring {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  @keyframes lds-ring {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  
#contact{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 50px 0;
    background: var(--amber);
}

.contact-form-container{
    width: 500px;
}

#contact-form{
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    padding: 20px;
    border-radius: 7px;
    /* background: var(--marmol); */
}

#contact-form form{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.field{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 5px 0;
    margin-bottom: 10px;
    width: 100%;
}

.field textarea{
    resize: vertical;
    min-height: 100px;
    width: 100%;
    box-sizing: border-box;
    padding: 10px;
    border-radius: 4px;
    border: 2px solid transparent;
    outline: none;
}

.field input{
    width: 100%;
    box-sizing: border-box;
    padding: 10px;
    border-radius: 4px;
    border: 2px solid transparent;
    outline: none;
}

.field input:focus,
.field textarea:focus{
    border: 2px solid var(--black);
}

.field label{
    font-family: var(--sans-pro);
    font-weight: bold;
    font-size: 18px;
    margin-bottom: 5px;
    margin-top: 5px;
}

/* button with loader */
.button-with-loader{
    width: 100%;
    display: flex;
    /* justify-content: center; */
    align-items: center;
    padding: 2px 0;
    margin-top: 7px;
}

.button-with-loader button{
    font-size: 18px;
    font-weight: bold;
    background: var(--white);
    padding: 8px 25px;
    border: 2px solid transparent;
    border-radius: 35px;
    cursor: pointer;
    transition: .3s;
    width: 100%;
    outline: none;
    height: 50px;
}

.button-with-loader button:hover{
    background: var(--amber);
    border-color: var(--black);
}


@media screen and (max-width: 600px){
    .contact-form-container{
        width: 300px;
    }
}
